import { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";

//internal import
import { SidebarContext } from "context/SidebarContext";
import SettingServices from "services/SettingServices";
import { notifyError, notifySuccess } from "utils/toast";
import { createBooking, updateBooking } from "services/Api";
import { useHistory, useLocation } from "react-router-dom";
import moment from "moment";

const useSettingSubmit = (
  selectedFile,
  activitySelectedOptions,
  mealsSelectedOptions,
  id
) => {
  const { setIsUpdate } = useContext(SidebarContext);
  const [isSave, setIsSave] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [imageUrl, setImageUrl] = useState([]);
  const history = useHistory();
  const [Dvalue, setDvalue] = useState(null);
  const [Evalue, setEvalue] = useState(null);
  const [Fvalue, setFvalue] = useState(null);
  const [selectFood, setSelectFood] = useState([]);
  const [selectActivity, setSelectActivity] = useState([]);
  const [foodCount, setFoodCount] = useState([]);
  const [actvityCount, setActivityCount] = useState([]);
  const [discountType, setDiscountType] = useState(false);

  const {
    register,
    handleSubmit,
    setValue,
    watch,
    reset,
    formState: { errors },
  } = useForm();
  const resetBtn = () => {
    console.log("fsefh");
    history.replace("/guest-list");
    reset();
  };
  const onSubmit = async (data) => {
    setIsSubmitting(true);
    console.log("fssdjh", data, Dvalue, Fvalue);
    try {
      // if (imageUrl.length === 0) {
      //   setTimeout(() => {
      //     setIsSubmitting(false);
      //   }, 2000);
      //   return notifyError("File is empty. Please select a valid file.");
      // } else {
      const propertyImages = imageUrl.map((item) => ({
        image: typeof item.image === "string" ? item.image : item.image.image,
      }));
      console.log(
        data,
        selectedFile,
        mealsSelectedOptions,
        activitySelectedOptions,
        "data"
      );
      const payload = {
        property_id: data?.PropertyId,
        // category: data?.category,
        package_id: data?.category !== "event" ? data.PackagesId : null,
        event_id: data?.category === "event" ? data.PackagesId : null,
        package_type: data?.category,
        guests: {
          name: data.fullName,
          email: data.emailId,
          dob: data?.dob,
          mobile: data.phonenumber,
          id_proof_type: data.typeIdProof,
          // id_doc_url: selectedFile,
          id_doc_url: propertyImages[0]?.image,
        },
        no_of_adults: data.noOfAdult,
        no_of_children: data.noOfChild ? data.noOfChild : 0,
        no_of_veg: data.noOfVeg ? data.noOfVeg : 0,
        no_of_nonveg: data.noOfNonVeg ? data.noOfNonVeg : 0,
        check_in_date: moment(Dvalue).format("YYYY-MM-DD"),
        check_in_time: data.cinTime,
        check_out_date: moment(Fvalue).format("YYYY-MM-DD"),
        check_out_time: data.coutTime,
        discount_type: discountType ? "percentage" : "fixed_amount",
        discount_value: data.discountPercentage,
        ala_cartes: foodCount?.map((item) => {
          return {
            id: item?.id,
            qty: item?.qty,
          };
        }),
        paid_activities: actvityCount?.map((item) => {
          return {
            id: item?.id,
            qty: item?.qty,
          };
        }),
        payment_type: data.payment_type,
        received_payment: data.received_payment ? data.received_payment : 0,
        PaymentMode: data.PaymentMode
      };

      if (data?.category === "stay") {
        payload.no_of_package = data?.noOfRoom;
      }
      let res = {};
      // if (id) {
      //   res = await updateBooking(payload);
      // } else
      res = await createBooking(payload, data?.id);
      console.log(payload)
      if (res.data.status == true) {
        //   getAllFoodList(1);
        //   closeDrawer();
        setIsSubmitting(false);
        history.replace("/guest-list");
        notifySuccess(res.data.message);
      }
    } catch (err) {
      notifyError(err ? err?.response?.data?.message : err.message);
      setIsSubmitting(false);
    }
  };
  //   useEffect(() => {
  //     (async () => {
  //       try {
  //         const res = await SettingServices.getGlobalSetting();
  //         // console.log("res>>>", res);
  //         if (res) {
  //           setIsSave(false);
  //           setValue(
  //             "number_of_image_per_product",
  //             res.number_of_image_per_product
  //           );

  //           setValue("shop_name", res.shop_name);
  //           setValue("address", res.address);
  //           setValue("company_name", res.company_name);
  //           setValue("vat_number", res.vat_number);
  //           setValue("post_code", res.post_code);
  //           setValue("contact", res.contact);
  //           setValue("email", res.email);
  //           setValue("website", res.website);
  //           setValue("receipt_size", res.receipt_size);
  //           setValue("default_currency", res.default_currency);
  //           setValue("default_time_zone", res?.default_time_zone);
  //           setValue("default_date_format", res?.default_date_format);
  //         }
  //       } catch (err) {
  //         notifyError(err ? err?.response?.data?.message : err.message);
  //       }
  //     })();
  //   }, [setValue]);

  return {
    errors,
    register,
    handleSubmit,
    onSubmit,
    resetBtn,
    isSave,
    Dvalue,
    setDvalue,
    Evalue,
    setEvalue,
    Fvalue,
    setFvalue,
    imageUrl,
    setImageUrl,
    watch,
    isSubmitting,
    setValue,
    selectFood,
    setSelectFood,
    selectActivity,
    setSelectActivity,
    foodCount,
    setFoodCount,
    actvityCount,
    setActivityCount,
    discountType,
    setDiscountType,
  };
};

export default useSettingSubmit;
