import spinnerLoadingImage from "assets/img/spinner.gif";
import InputAreaTwo from "components/form/InputAreaTwo";
import PageTitle from "components/Typography/pageTitleButton";
import useSettingSubmit from "hooks/useGuestSubmit";
import { useEffect, useState } from "react";
import {
  createCart,
  getAllActivitiesList,
  getAllCommonProperyList,
  getAllCommongetPackagesList,
  getAllKarteList,
  uploadDocImage,
} from "services/Api";
import { useRef } from "react";
import { notifyError, notifySuccess } from "utils/toast";
import Uploader from "components/image-uploader/Uploader";
import moment from "moment";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Multiselect from "multiselect-react-dropdown";
import LabelArea from "components/form/LabelArea";
import { FiMinusCircle, FiPlusCircle } from "react-icons/fi";
import InputValue from "components/form/InputValue";
import SwitchToggleFour from "components/form/SwitchToggleFour";
